.about-window{
    width: 60vw;
    height: auto;
    margin-top: 10vh;
}
.aboutbg{
    height: 60vh;
    width: 60vw;
}
.about-img{
    opacity: 60%;
    height: 40vh;
    width: 30vh;
    
}
.container {
    position: relative;
    text-align: center;
    color: white;
    left: 20vw;
    
  }
.top-centered{
    color: black;
    position: absolute;
    left: 50%;
    font-size: 60px;
    transform: translate(-39%, 178%);
    font-family: 'Caveat', cursive;
    
  }
.bottom-centered {
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
  }
.profile{
    height: 150px;
    width: 150px;
    border-radius: 50%;
    box-shadow: 0 0 25px 1px rgba(0, 0, 0, .3);
}
.container-bot{
    position: relative;
    text-align: center;
    left: 21vw;
    top:12vh;
    font-family: "Sofia", sans-serif;
  font-size: 20px;
  text-shadow: 3px 3px 3px #ababab;
  
}