.col{
    margin-top: 20vh;
    display: flex;
    justify-content: center;
}


.container-main{
    display: flex;
    justify-content: center;
}
.middle-back{
    width: 80%;
}
.info-head{
    font-size: large;
}

.head-title{
    margin-top: 10vh;
    
}

a{
    color: black;
    text-decoration: none;
}
a:hover{font-size: 110%;
        color: black;
}

.card{
    height: 60%;
    width: 60%;
    height: fit-content;
    transition: 0.02s (ease-in-out);
    box-shadow: 0 0 25px 1px rgba(0, 0, 0, .3);
}

.card-img-top{
    height: 100%;
    width:100% ;
    box-shadow: 0 0 25px 1px rgba(0, 0, 0, .3);
    transition: .5s;
}
.card-img-top::after{
    content: '';
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    width: 500px;
    height: 500px;
    transform: translate(-140%, -50%);
    background-color: blue;
    opacity: 0.8;
    border-radius: 50%;
    transition: .8s
}
.card-img-top:hover:after {
    transform: translate(-50%, -50%)
}

.card-img-top:hover img {
    transform: translate(-50%, -50%) scale(1.3) rotate(20deg)
}

.Loading{
    padding-top: 20vh;
}